import React, {Component} from 'react';
import {Consumer} from '../../Context';
import uuid from 'uuid';
import {ADD_CONTACT} from '../../ActionType';
import TextInputGroup from '../layouts/TextInputGroup';
import axios from 'axios'
import { async } from 'q';

class AddContact extends Component {
  state = {
    name: '',
    email: '',
    phone: '',
    errors: {},
  };

  onChange = e => this.setState({[e.target.name]: e.target.value});

  onSubmit =async (dispatch, e) => {
    e.preventDefault();
    console.log(this.state);

    const {name, email, phone} = this.state;

    //validate

    if (name === '') {
      this.setState({errors: {name: 'Name is requeired'}});
      return;
    }

    if (email === '') {
      this.setState({errors: {email: 'Email is requeired'}});
      return;
    }

    if (phone === '') {
      this.setState({errors: {phone: 'Phone is requeired'}});
      return;
    }

    const newContact = {
    
      name,
      email,
      phone,
      errors: {},
    };

    const res = await axios.post('https://jsonplaceholder.typicode.com/users', newContact)
      
    dispatch({
      type: ADD_CONTACT,
      payload: res.data,
    })


    

    this.setState({
      name: '',
      email: '',
      phone: '',
    });

    this.props.history.push('/');
  };

  render() {
    const {name, email, phone, errors} = this.state;

    return (
      <Consumer>
        {value => {
          const {dispatch} = value;

          return (
            <div className="card mb-3">
              <div className="card-header">Add contact</div>
              <div className="card-body">
                <form onSubmit={this.onSubmit.bind(this, dispatch)}>
                  <TextInputGroup
                    placeholder="Enter your full name"
                    label="Name"
                    value={name}
                    onChange={this.onChange}
                    name="name"
                    error={errors.name}
                  />

                  <TextInputGroup
                    placeholder="Enter email"
                    label="Email"
                    type="email"
                    value={email}
                    onChange={this.onChange}
                    name="email"
                    error={errors.email}
                  />

                  <TextInputGroup
                    placeholder="Enter phone"
                    label="Phone"
                    value={phone}
                    onChange={this.onChange}
                    name="phone"
                    error={errors.phone}
                  />

                  <input
                    type="submit"
                    value="Add Contact"
                    className="btn btn-light mt-3 btn-block"
                  />
                </form>
              </div>
            </div>
          );
        }}
      </Consumer>
    );
  }
}
export default AddContact;
